@tailwind base;
@tailwind components;
@tailwind utilities;

.ranking-1 {
  position: relative;
}

.ranking-1::before {
  content: "ðŸ‘‘";
  font-size: 1.5em;
  position: absolute;
  white-space: pre;
  display: inline;
  top: 6px;
  left: 8px;
  transform: translate(-50%, -22px) rotate(-30deg);
}

.error-message {
  font-weight: bold;
  color: red; /* or any color intended for error messages */
}

.success-message {
  font-weight: bold;
  color: rgb(17, 177, 110); /* or any color intended for success messages */
}

.input-field {
  padding: 10px 15px;
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  font-size: 16px;
  outline: none;
  width: 100%;
  box-sizing: border-box;
}

.input-field-error {
  border-color: red;
}

.input-field-valid {
  border-color: #ccc;
}

.winner-selection-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
}

.winner-selection-title {
  font-size: 1.25rem; /* 20px */
  margin-bottom: 0.5rem;
}